import React, { useEffect } from "react";
import { Helmet } from "react-helmet";
import Container from "../Components/Container";
import Header from "../Components/Header";
import Contents from "../Components/Contents";
import Card from "../Components/Card";
import ListHeader from "../Components/ListHeader";
import { headerArray, dataArray } from "../Assets/Data/About_Array";

const About = ({ history }) => {
  const contentsArray = dataArray();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <Container>
      <Helmet>
        <title>{headerArray.title}</title>
      </Helmet>
      <Header
        history={history}
        stage={headerArray.stage}
        title={headerArray.title}
        channelArray={headerArray.channelArray}
        desc={headerArray.desc}
        author={headerArray.author}
        authorUrl={headerArray.authorUrl}
      ></Header>
      <Contents stage={"About"}>
        <ListHeader listTitle={"Steps"}></ListHeader>
        {contentsArray.map((data, index) => (
          <Card
            stage="About"
            textColor="white"
            leftText={`Step ${index + 1}`}
            title={data.title}
            subTitle={data.subTitle}
            desc={data.desc}
            img={data.img}
            imgType={data.imgType ? data.imgType : null}
            category={`${index + 1}/${contentsArray.length}`}
          ></Card>
        ))}
      </Contents>
    </Container>
  );
};

export default About;
