import React, { useEffect } from "react";
import Container from "../Components/Container";
import Header from "../Components/Header";
import Contents from "../Components/Contents";
import Card from "../Components/Card";
import { headerArray, dataArray } from "../Assets/Data/Mojitto_Array";
import { Helmet } from "react-helmet";
import ListHeader from "../Components/ListHeader";

const Mojitto = ({ history }) => {
  const contentsArray = dataArray(headerArray.themeColor);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <Container>
      <Helmet>
        <title>{headerArray.title}</title>
      </Helmet>
      <Header
        stage={headerArray.stage}
        title={headerArray.title}
        desc={headerArray.desc}
        channelArray={headerArray.channelArray}
        author={headerArray.author}
        authorUrl={headerArray.authorUrl}
      ></Header>
      <Contents stage="Detail" history={history} main={headerArray.main}>
        <ListHeader listTitle="About Montage"></ListHeader>
        {contentsArray.map((data, index) => (
          <Card
            key={index}
            stage="Detail"
            textColor="white"
            leftText={`#${index + 1}`}
            title={data.title}
            subTitle={data.subTitle}
            desc={data.desc}
            img={data.img}
            imgType={data.imgType ? data.imgType : null}
            category={`${index + 1}/${contentsArray.length}`}
          ></Card>
        ))}
      </Contents>
    </Container>
  );
};

export default Mojitto;
